.navbar
{
	background: #FFF;
	grid-area: nav;
	height: 60px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 30px 0 30px;
	border-bottom: 1px solid lightgray;
}

.nav-icon
{
	display: none;
}

.nav-icon > i
{
	font-size: 26px;
	color: #a5aaad;
}

.navbar-left > a
{
	margin-right: 30px;
	text-decoration: none;
	color: #a5aaad;
	font-size: 15px;
	font-weight: 700;
}

.navbar-left .active-link
{
	color: #265acc;
	border-bottom: 3px solid #265acc;
	padding-bottom: 12px;
}

.navbar-rigth
{
	display: flex;
	justify-content: center;
	align-items: center;
}

.navbar-rigth > a
{
	margin-left: 20px;
	text-decoration: none;
}

.navbar-rigth > a > i 
{
	color: #a5aaad;
	font-size: 16px;
	border-radius: 50px;
	background: #FFF;
	box-shadow: 2px 2px 5px #d9d9d9, -2px -2px 5px #FFF;
	padding: 7px;
}

@media only screen and (max-width: 978px)
{
	.nav-icon
	{
		display: inline;
	}
}

@media only screen and (max-width: 480px)
{
	.navbar-left
	{
		display: none;
	}
}