#sidebar
{
	background-color: #020509;
	grid-area: sidebar;
	overflow-y: auto;
	padding: 20px;
	-webkit-transition:  all 0.5s;
	transition: all 0.5s;
}

.sidebar-title
{
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: #f3f4f6;
	margin-bottom: 30px;
}

.sidebar-img
{
	display: flex;
	align-items: center;
}

.sidebar-title > div > img
{
	width: 55px;
	object-fit: contain;
	border-radius: 50%;
}

.sidebar-title > div > h1
{
	font-size: 18px;
	display: inline;
	margin-left: 15px;
}

.sidebar-title > i
{
	font-size: 18px;
	display: none;
}

.sidebar-menu > h2
{
	color: #3ea175;
	font-size: 16px;
	margin-top: 15px;
	margin-bottom: 5px;
	padding: 0 10px;
	font-weight: 700;
}

.sidebar-link
{
	color: #f3f4f6;
	padding: 10px;
	border-radius: 3px;
	margin-bottom: 5px;
}

.active-menu-link
{
	background: rgba(62, 161, 117, 0.3);
	color: #3ea175;
}

.active-menu-link a
{
	color: #3ea175 !important;
}

.sidebar-link > a 
{
	text-decoration: none;
	color: #a5aaad;
	font-weight: 700;
}

.sidebar-link a:focus
{
	padding: 7px;
	background: rgba(62, 161, 117, 0.3);
	border-radius: 3px;
}

.sidebar-link i 
{
	margin-right: 10px;
	font-size: 18px;
}

.sidebar-logout 
{
	margin-top: 20px;
	padding: 10px;
	color: #e65061;
}

.sidebar-logout > a
{
	text-decoration: none;
	color: #e65061;
	font-weight: 700;
	text-transform: uppercase;
}

.sidebar-logout > i
{
	margin-right: 10px;
	font-size: 18px;
}

.sidebar-responsive
{
	display: inline !important;
	z-index: 9999 !important;
	left: 0 !important;
	position: absolute;
}

@media only screen and (max-width:  978px)
{
	#sidebar
	{
		display: none;
	}
	.sidebar-title > i
	{
		display: inline;
	}
}