@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
/* Estilizações da scrollbars */

::webkit-scrollbar
{
  width: 5px;
  height: 6px;
}

::webkit-scrollbar-track
{
  box-shadow: inset 0 0 5px #a5aaad;
  border-radius: 10px;
}

::webkit-scrollbar-thumb
{
  background: #3ea175;
  border-radius: 10px;
}

::webkit-scrollbar-thumb:hover
{
  background: #a5aaad;
}

/* Estilizações gerais */

*
{
  margin: 0;
  padding: 0;
}

body
{
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
}

.text-primary-p
{
  color: #a5aaad;
  font-size: 14px;
  font-weight: 700;
}

.font-bold
{
  font-weight: 700;
}

.text-title
{
  color: #2e4a66;
}

.text-lightblue
{
  color: #469cac;
}

.text-red
{
  color: #cc3d38;
}

.text-yellow
{
  color: #a98921;
}

.text-green
{
  color: #3b9698;
}

.container
{
  display: grid;
  height: 100vh;
  grid-template-columns: 0.8fr repeat(3, 1fr);
  grid-template-rows: 0.2fr 3fr;
  grid-template-areas:
  'sidebar nav nav nav'
  'sidebar main main main';
}


/* Responsavidade */

@media only screen and (max-width:  978px)
{
  .container
  {
    grid-template-columns: 1fr;
    grid-template-rows: 0.2fr 3fr;
    grid-template-areas: "nav" "main";
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#sidebar
{
	background-color: #020509;
	grid-area: sidebar;
	overflow-y: auto;
	padding: 20px;
	transition: all 0.5s;
}

.sidebar-title
{
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: #f3f4f6;
	margin-bottom: 30px;
}

.sidebar-img
{
	display: flex;
	align-items: center;
}

.sidebar-title > div > img
{
	width: 55px;
	object-fit: contain;
	border-radius: 50%;
}

.sidebar-title > div > h1
{
	font-size: 18px;
	display: inline;
	margin-left: 15px;
}

.sidebar-title > i
{
	font-size: 18px;
	display: none;
}

.sidebar-menu > h2
{
	color: #3ea175;
	font-size: 16px;
	margin-top: 15px;
	margin-bottom: 5px;
	padding: 0 10px;
	font-weight: 700;
}

.sidebar-link
{
	color: #f3f4f6;
	padding: 10px;
	border-radius: 3px;
	margin-bottom: 5px;
}

.active-menu-link
{
	background: rgba(62, 161, 117, 0.3);
	color: #3ea175;
}

.active-menu-link a
{
	color: #3ea175 !important;
}

.sidebar-link > a 
{
	text-decoration: none;
	color: #a5aaad;
	font-weight: 700;
}

.sidebar-link a:focus
{
	padding: 7px;
	background: rgba(62, 161, 117, 0.3);
	border-radius: 3px;
}

.sidebar-link i 
{
	margin-right: 10px;
	font-size: 18px;
}

.sidebar-logout 
{
	margin-top: 20px;
	padding: 10px;
	color: #e65061;
}

.sidebar-logout > a
{
	text-decoration: none;
	color: #e65061;
	font-weight: 700;
	text-transform: uppercase;
}

.sidebar-logout > i
{
	margin-right: 10px;
	font-size: 18px;
}

.sidebar-responsive
{
	display: inline !important;
	z-index: 9999 !important;
	left: 0 !important;
	position: absolute;
}

@media only screen and (max-width:  978px)
{
	#sidebar
	{
		display: none;
	}
	.sidebar-title > i
	{
		display: inline;
	}
}
.navbar
{
	background: #FFF;
	grid-area: nav;
	height: 60px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 30px 0 30px;
	border-bottom: 1px solid lightgray;
}

.nav-icon
{
	display: none;
}

.nav-icon > i
{
	font-size: 26px;
	color: #a5aaad;
}

.navbar-left > a
{
	margin-right: 30px;
	text-decoration: none;
	color: #a5aaad;
	font-size: 15px;
	font-weight: 700;
}

.navbar-left .active-link
{
	color: #265acc;
	border-bottom: 3px solid #265acc;
	padding-bottom: 12px;
}

.navbar-rigth
{
	display: flex;
	justify-content: center;
	align-items: center;
}

.navbar-rigth > a
{
	margin-left: 20px;
	text-decoration: none;
}

.navbar-rigth > a > i 
{
	color: #a5aaad;
	font-size: 16px;
	border-radius: 50px;
	background: #FFF;
	box-shadow: 2px 2px 5px #d9d9d9, -2px -2px 5px #FFF;
	padding: 7px;
}

@media only screen and (max-width: 978px)
{
	.nav-icon
	{
		display: inline;
	}
}

@media only screen and (max-width: 480px)
{
	.navbar-left
	{
		display: none;
	}
}
main
{
	background: #f3f4f6;
	grid-area: main;
	overflow-y: auto;
}

::-webkit-scrollbar
{
	width: 0px;
	background: transparent;
}

.main-container
{
	padding: 20px 35px;
}

.main-title
{
	display: flex;
	align-items: center;
}

.main-title > img
{
	max-height: 100px;
	object-fit: contain;
	margin-right: 20px;
}

.main-greeting > h1
{
	font-size: 24px;
	color: #2e3a66;
	margin-bottom: 5px
}

.main-greeting > p 
{
	font-size: 14px;
	font-weight: 700;
	color: #a5aaad;
}

.main-cards
{
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap:  30px;
	gap:  30px;
	margin: 20px 0;
}

.card
{
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	height: 70px;
	padding: 25px;
	border-radius: 5px;
	background-color: #FFF;
	box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #FFF;
}

.card-inner
{
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.card-inner > span
{
	font-size: 25px;
}

.charts
{
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 30px;
	gap: 30px;
	margin-top: 50px;
}

.charts-left
{
	padding: 25px;
	border-radius: 5px;
	background: #FFF;
	box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #FFF;
}

.charts-left-title
{
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.charts-left-title > div > h1 
{
	font-size: 24px;
	color: #2e4a66;
	margin-bottom: 5px;
}

.charts-left-title > div > p
{
	font-size: 14px;
	font-weight: 700;
	color: #a5aaad;
}
.charts-left-title > i
{
	color: #FFF;
	font-size: 20px;
	background: #FFC100;
	border-radius: 200px 0px 200px 200px;
	border-radius: 0px solid #000;
	padding: 15px;
}

.charts-right
{
	padding: 25px;
	border-radius: 5px;
	background: #FFF;
	box-shadow: 5px 5px 13px #ededed , -5px -5px 13px #FFF;
}

.charts-right-title 
{
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.charts-right-title > div > h1
{
	font-size: 24px;
	color: #2e4a66;
	margin-bottom: 5px;
}

.charts-right-title > div > p
{
	font-size: 14px;
	font-weight: 700;
	color: #a5aaad;
}

.charts-right-title > i
{
	color: #FFF;
	font-size: 20px;
	background: #39447a;
	border-radius: 200px 0px 200px 200px;
	border-radius: 0px solid #000;
	padding: 15px;
}

