	padding: 15px;
}*/

.charts-right-cards
{
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 20px;
	gap: 20px;
	margin-top: 30px;
}

.card1
{
	background: #d1ecf1;
	color: #35a4ba;
	text-align: center;
	padding: 25px;
	border-radius: 5px;
	font-size: 14px;
}

.card2
{
	background: #d2f9ee;
	color: #38e1b0;
	text-align: center;
	padding: 25px;
	border-radius: 5px;
	font-size: 14px;
}

.card3
{
	background: #d6d8d9;
	color: #3a3e41;
	text-align: center;
	padding: 25px;
	border-radius: 5px;
	font-size: 14px;
}

.card4
{
	background: #fddcdf;
	color: #f65a6f;
	text-align: center;
	padding: 25px;
	border-radius: 5px;
	font-size: 14px;
}

@media only screen and (max-width: 855px)
{
	.main-cards
	{
		grid-template-columns: 1fr;
		grid-gap: 10px;
		gap: 10px;
		margin-bottom: 0;
	}

	.charts
	{
		grid-template-columns: 1fr;
		margin-top: 30px;
		justify-content: center;
	}

	.charts-right-cards
	{
		grid-template-columns: 1fr;
		align-items: center;
		margin-top: 30px;
	}
}
